/**
* 案件->票据管理
* @module 案件
* @desc 票据管理表格
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="search-wrop">
            <lj-search-from  :searchObj="searchObj" @searchList="searchList" />
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}" v-loading='loading'
                  @selection-change="handleSelectionChange" @row-click="seeFunction"
                  border size="mini">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column align="center" label="面额" prop="quotaInvoiceAmount" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="起讫号" prop="quotaInvoiceNum" show-overflow-tooltip/>
            <el-table-column align="center" label="领用部门" prop="collarOrgName" show-overflow-tooltip />
            <el-table-column align="center" label="领用人" prop="collarOrgPerson" show-overflow-tooltip width="100"/>
            <el-table-column align="center" label="领用日期" prop="collarTime" show-overflow-tooltip>
                 <template slot-scope="scope">
                    {{scope.row.collarTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="待上缴份数" prop="quotaInvoiceCount" show-overflow-tooltip width="100"/>
            <el-table-column align="center" label="上缴份数" prop="submittedNum" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="操作" width="80">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-search btn-css"
                                 type="success" v-if="buttonCheck('see')"></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"  @getData="getList"
                       class="page-css"
        />

        <!-- 查看 -->
        <seecontent :caseProcessList="caseProcessList" :caseTypeList='caseTypeList' :show.sync="seeObj.show"
                    :title="seeObj.title" :id="seeObj.editId" @getList="getList"/>
               
    </div>
</template>

<script>
    import LjPagination from '../../../../../components/LjPagination'
    import LjSearchFrom from '../../../../../components/LjSearchForm'
    import seecontent from "./seecontent";
    import {
        apiCaseQuotaInvoiceBatchDelete,
        apiCaseQuotaInvoiceDelete,
        apiCaseQuotaCollaruserQueryBeTurnedCollaruser
    } from "../../../../../api/case";
    import {formatDate,quotaStatus} from "../../../../../until/wordBook";

    export default {
        name:"tobeturned",
        data() {
            return {
                caseProcessList: [],
                docStyleArr: [],
                loading: true,
                searchObj: [
                    {type: 'input', name: 'quotaInvoiceStart', label: '开始发票号'},
                    {type: 'input', name: 'quotaInvoiceEnd', label: '结束发票号'},
                    {
                        type: 'date',
                        dateType: 'daterange',
                        name: 'timeRange',
                        name1: 'startTime',
                        name2: 'endTime',
                        options: [],
                        label: '时间段',
                        span: 12
                    },
                ],
                //弹出框数据
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                caseTypeList: [],
                form: {
                    pageSize: 20,
                    pageNum: 1,
                    quotaStatus:2
                },
                total: 0,
                list: [],
                idstr: '',
            }
        },
        components: {
             LjPagination, LjSearchFrom,seecontent
        },


        methods: {
            //批量选择
            handleSelectionChange(val) {
                let idarr = [];
                val.forEach(item => {
                    idarr.push(item.quotaId);
                });
                this.idstr = idarr.join(',')
            },

            //批量上传
            onImport() {
                this.seeObj.title = '批量上传';
                this.seeObj.show = true;
            },

            //查询+搜索
            getList() {
                apiCaseQuotaCollaruserQueryBeTurnedCollaruser(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                })
            },

            //查看
            seeFunction(row) {

                this.seeObj.editId = row.quotaId;
                this.seeObj.title = '查看';
                this.seeObj.show = true;
            },

            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = '新增票据';
                this.editObj.show = true;
            },

            //删除按钮事件
            delateFunction(index, data) {
                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseQuotaInvoiceDelete(data.quotaId).then(() => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },

            //批量删除
            onDel() {

                this.$confirm('此操作将永久删除这些数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseQuotaInvoiceBatchDelete({quotaIds: this.idstr}).then(() => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    })
                })
            },
            
            //搜索点击
            searchList(data) {  //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form = JSON.parse(JSON.stringify(data));
                this.form.pageNum = 1;
                this.form.quotaStatus=2;
                apiCaseQuotaCollaruserQueryBeTurnedCollaruser(this.form).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                })
            },
        },

        filters: {
           formatDate(val){
              return formatDate(val,"yyyy-MM-dd")
           }

        },
        created() {
            this.getList();

        },
    }
</script>

<style scoped>
    .search-wrop {
        margin-bottom: 10px
    }

    .btn-css {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
